export function getCategoryLabel(category: keyof typeof CategoryOfProducts): string {
  const labels: { [key: string]: string } = {
    PRODUCE: 'Hortifrti',
    BAKERY: 'Padaria',
    DAIRY: 'Laticnios',
    MEAT_AND_SEAFOOD: 'Carnes e Frutos do Mar',
    FROZEN_FOODS: 'Congelados',
    BEVERAGES: 'Bebidas',
    SNACKS: 'Lanches',
    PANTRY: 'Despensa',
    HOUSEHOLD: 'Utilidades Domsticas',
    HEALTH_AND_BEAUTY: 'Sade e Beleza',
    BABY: 'Bebs',
    PETS: 'Animais de Estimao',
    ALCOHOL: 'lcool',
    PHARMACY: 'Farmcia',
    ELECTRONICS: 'Eletrnicos',
    CLOTHING: 'Vesturio',
    SEASONAL: 'Sazonais',
    DELI: 'Rotisseria',
  };
  return labels[category] || category;
}